@import '../../sass/variables';

.container-modal {
  position: absolute;
  height: auto;
  padding: 20px 198px 24px 60px;
  background: #f2f2f2;
  border: 1px solid #f2f2f2;
  box-sizing: border-box;
  box-shadow: 0px 1px 4px rgba(60, 52, 135, 0.15);
  z-index: 5;
}

.modal_identification {
  @include style-text;
  line-height: 140%;
  text-transform: uppercase;
  color: $menu-color;
  display: flex;
  align-items: center;
  & > b {
    color: $pagination-color;
  }
  margin-bottom: 12px;
}

.modal_container-button-filter {
  display: flex;
  align-items: baseline;
  margin-bottom: 12px;
}

.modal_container-button-filter_button-filter {
  font-family: 'Rubik';
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 140%;
  text-transform: uppercase;
  color: #5b5b5b;
  background: #ffffff;
  border: 1px solid $filter-color;
  box-sizing: border-box;
  padding: 6px 18px 6px 12px;
  margin-right: 16px;
  display: flex;
  align-items: center;
}

.modal_identification_student-name {
  margin-right: 15px;
}

.modal_container-button-filter_arrow {
  fill: inherit;
  margin-left: 10px;
}

.modal_container-date_data-filter {
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom-color: #c0c0c0;
  background-color: inherit;
  padding: 0 0 7px 0;
  @include style-text;
  text-transform: uppercase;
  color: $filter-color;
  width: 199px;
}

.modal_container-date {
  position: relative;
  display: flex;
  align-items: center;
}

.modal_container-date_data {
  fill: inherit;
  margin-left: 14px;
  position: absolute;
  bottom: 10px;
  right: 0;
}

.modal_line {
  width: 1102px;
  height: 1px;
  background-color: $active-menu-color;
  margin-bottom: 12px;
}

.modal_container-score {
  display: flex;
  align-items: center;
}

.modal_container-speed {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}

.modal_score {
  @include style-text;
  font-size: 14px;
  text-transform: uppercase;
  color: $menu-color;
}

.modal_speed {
  font-family: 'Rubik';
  font-style: normal;
  font-weight: $font-weight;
  font-size: $font-size;
  line-height: 140%;
  text-transform: uppercase;
  color: $menu-color;
}

.modal_ideal-text {
  @include style-text;
  text-transform: uppercase;
  color: #4285f4;
}

