.modal_identification_arrow-close-button {
  position: absolute;
  border: none;
  margin-left: auto;
  background-color: inherit;
  top: 22px;
  right: 15px;
}

.modal_identification_arrow-close {
  fill: inherit;
  transform: rotate(180deg);
  padding: 5px 5px;
  cursor: pointer;
}
