.archive_container-parent-name {
  font-family: 'Rubik';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  color: #c0c0c0;
  display: inline-block;
  width: 250px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.arhive_container-parent-name_parent-name {
  font-family: 'Rubik';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  color: #c0c0c0;
  display: inline-block;
  width: 250px;
}

.archived {
  font-family: 'Rubik';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  text-transform: uppercase;
  color: #5b5b5b;
  margin-left: 24px;
}

.archive {
  padding-left: 0;
  margin: 0 0;
}

.archive_list {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 14px 21px 14px 23px;
  background: #f9f9f9;
  -webkit-box-shadow: 0px 1px 4px rgba(60, 52, 135, 0.15);
          box-shadow: 0px 1px 4px rgba(60, 52, 135, 0.15);
}

.archive_checkbox {
  color: #c0c0c0;
  margin-right: 26px;
}

